* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  scroll-behavior: smooth;
  background-color: #000;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: Arial, sans-serif;
  display: flex;
}

.side-button {
  color: #fff;
  cursor: pointer;
  z-index: 1000;
  text-align: center;
  background: #8b4513;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  width: 90px;
  padding: 12px 15px;
  font-size: 14px;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
  box-shadow: 0 4px 8px #0000004d;
}

.side-button:hover {
  background: sienna;
  transform: translateY(-50%)scale(1.05);
}

.logo-image {
  object-fit: contain;
  border-radius: 6px;
  width: 80px;
  height: 80px;
  margin-bottom: 2px;
}

.nav-buttons {
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  background: #1e1e1ef2;
  border-radius: 8px;
  flex-direction: column;
  gap: 10px;
  width: 400px;
  padding: 20px;
  transition: all .3s;
  display: flex;
  position: fixed;
  top: 50%;
  right: 80px;
  transform: translateY(-50%);
  box-shadow: 0 4px 12px #0000004d;
}

.nav-buttons.active {
  opacity: 1;
  visibility: visible;
}

.route-item {
  background: #282828e6;
  border-radius: 5px;
  align-items: center;
  gap: 15px;
  width: 100%;
  padding: 12px;
  transition: background .3s;
  display: flex;
}

.route-item:hover {
  background: #3c3c3ce6;
}

.speed {
  text-align: center;
  color: #fff;
  background: #1a1a1a;
  border-radius: 4px;
  min-width: 70px;
  padding: 5px 10px;
  font-weight: bold;
}

.route-name {
  text-align: center;
  color: #fff;
  flex-grow: 1;
  font-size: 18px;
}

.download-btn {
  color: #fff;
  cursor: pointer;
  background: #8b4513;
  border: none;
  border-radius: 4px;
  padding: 10px 25px;
  font-size: 16px;
  transition: background-color .3s;
}

.download-btn:hover {
  background: sienna;
}

.web-version {
  background: #8b4513;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  animation: 1s ease-in-out fadeIn;
}

.image-container {
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px;
  display: flex;
}

.main-image {
  object-fit: contain;
  border-radius: 8px;
  width: 500px;
  height: 1400px;
  transition: transform .3s;
  box-shadow: 0 8px 16px #0000001a;
}

.main-image:hover {
  transform: translateY(-5px);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (width <= 768px) {
  .main-image {
    width: 100%;
    max-width: 500px;
    height: auto;
  }

  .container {
    padding: 10px;
  }

  .nav-buttons {
    width: 300px;
    padding: 15px;
    right: 20px;
  }

  .route-item {
    flex-wrap: wrap;
    padding: 10px;
  }

  .side-button {
    padding: 12px;
    right: 10px;
  }

  .logo-image {
    width: 45px;
    height: 45px;
  }
}

.loader {
  z-index: 2000;
  background-color: #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner {
  border: 5px solid #8b45134d;
  border-top-color: #8b4513;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-bottom: 15px;
  animation: 1s ease-in-out infinite spin;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.copyright {
  text-align: center;
  color: #fff9;
  width: 100%;
  padding: 5px 0;
  font-size: 12px;
  position: fixed;
  bottom: 10px;
  left: 0;
}
